/* CSS Variables for dark theme only */
:root {
  /* Dark theme colors - slightly lighter version of #0d1117 with green accent */
  --bg-primary: #151b23;
  --bg-secondary: #1c2329;
  --card-bg: rgba(33, 38, 45, 0.8);
  --text-color: #f0f6fc;
  --text-secondary: #8b949e;
  --border-color: rgba(255, 255, 255, 0.1);
  --hover-bg: rgba(255, 255, 255, 0.05);
  --hover-color: rgba(255, 255, 255, 0.08);
  --input-background: rgba(33, 38, 45, 0.8);
  --input-bg: rgba(33, 38, 45, 0.8);
  --modal-background: rgba(33, 38, 45, 0.95);
  --primary-color: #22c55e;
  --primary-color-light: rgba(34, 197, 94, 0.15);
  --primary-color-10: rgba(34, 197, 94, 0.1);
  --primary-color-20: rgba(34, 197, 94, 0.2);
  --primary-color-dark: #16a34a;
  --primary-hover: #16a34a;
  --nav-bg: rgba(21, 27, 35, 0.95);
  --nav-bg-rgb: 21, 27, 35;
  --footer-bg: #1c2329;
  --shadow: 0 2px 12px rgba(0, 0, 0, 0.4);
  --shadow-hover: 0 4px 20px rgba(0, 0, 0, 0.5);

  /* Success, warning, danger colors */
  --success-color: #22c55e;
  --warning-color: #f59e0b;
  --danger-color: #ef4444;
  --info-color: #06b6d4;

  /* Border radius */
  --border-radius: 4px;
  --border-radius-lg: 8px;

  /* Transitions */
  --transition-fast: 0.15s ease;
  --transition-normal: 0.25s ease;
  --transition-slow: 0.4s ease;
}

html {
  box-sizing: border-box;
  font-size: 16px;
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Inter', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:
    /* Тонкий зеленый оттенок поверх темного фона */
    linear-gradient(135deg,
      rgba(34, 197, 94, 0.02) 0%,
      transparent 30%,
      rgba(34, 197, 94, 0.01) 100%),
    /* Основной темный фон - светлее #0d1117 */
    #151b23;
  color: var(--text-color, #f0f6fc);
  line-height: 1.6;
  font-size: 16px;
  transition: background-color var(--transition-normal, 0.25s ease), color var(--transition-normal, 0.25s ease);
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
  background:
    /* Тонкий зеленый оттенок поверх темного фона */
    linear-gradient(135deg,
      rgba(34, 197, 94, 0.02) 0%,
      transparent 30%,
      rgba(34, 197, 94, 0.01) 100%),
    /* Основной темный фон - светлее #0d1117 */
    #151b23;
  position: relative;
}

/* Navigation */
.main-nav {
  background: var(--nav-bg, rgba(21, 27, 35, 0.95));
  backdrop-filter: blur(12px);
  border-bottom: 1px solid var(--border-color, rgba(255, 255, 255, 0.1));
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: var(--shadow, 0 2px 12px rgba(0, 0, 0, 0.4));
  transition: all 0.3s ease;
  width: 100%;
  max-width: 100vw;
}

/* Скролл-состояние навбара (только для мобильных) */
@media (max-width: 768px) {
  .main-nav.nav-scrolled {
    background: rgba(var(--nav-bg-rgb), 0.95);
    backdrop-filter: blur(20px);
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
  }

  .main-nav.nav-scrolled .nav-container {
    height: 60px;
  }

  .main-nav.nav-scrolled .nav-center {
    gap: 10px;
  }

  .main-nav.nav-scrolled .brand-logo {
    width: 40px;
    height: 40px;
  }

  .main-nav.nav-scrolled .nav-link {
    padding: 8px 12px;
  }

  .main-nav.nav-scrolled .nav-text {
    display: none;
  }

  .main-nav.nav-scrolled .nav-icon {
    font-size: 18px;
  }

  .main-nav.nav-scrolled .nav-brand {
    padding-bottom: 0;
    border-bottom: none;
  }
}

.nav-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  transition: height 0.3s ease;
}

.nav-center {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 25px;
  flex-wrap: wrap;
  transition: gap 0.3s ease;
}

.nav-brand {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 15px;
  transition: all 0.3s ease;
}

.nav-brand {
  display: flex;
  align-items: center;
}

.brand-link {
  display: flex;
  align-items: center;
  gap: 12px;
  text-decoration: none;
  color: var(--text-color);
  font-weight: 700;
  font-size: 24px;
  transition: color 0.2s ease;
}

.brand-link:hover {
  color: var(--primary-color);
}

.brand-logo {
  width: 80px;
  height: 80px;
  object-fit: contain;
  transition: all 0.3s ease;
}

.brand-logo:hover {
  transform: scale(1.05);
}

.brand-icon {
  font-size: 28px;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 32px;
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;
  color: var(--text-color);
  font-weight: 600;
  font-size: 15px;
  padding: 12px 20px;
  border-radius: 10px;
  transition: all 0.3s ease;
  background: var(--card-bg);
  border: 2px solid var(--border-color);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.nav-link:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
  border-color: var(--primary-color);
}

.nav-link.active {
  color: var(--primary-color);
  background: var(--primary-color-light);
  border-color: var(--primary-color);
}

.nav-icon {
  font-size: 18px;
  transition: all 0.3s ease;
}

.nav-link:hover .nav-icon {
  transform: scale(1.1);
}

.nav-text {
  transition: all 0.3s ease;
}

.nav-actions {
  display: flex;
  align-items: center;
  gap: 16px;
}

/* Main content */
.main-content {
  flex: 1;
  min-height: calc(100vh - 136px);
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  overflow-x: hidden;
  position: relative;
  background: transparent;
}

.main-content.login-layout {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  background: var(--bg-primary);
  min-height: 100vh;
  max-width: none;
  margin: 0;
}

/* Footer */
.main-footer {
  background: var(--footer-bg);
  border-top: 1px solid var(--border-color);
  margin-top: auto;
}

.footer-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 48px 24px 24px;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 40px;
  margin-bottom: 40px;
}

.footer-section h3,
.footer-section h4 {
  color: var(--text-color);
  margin-bottom: 20px;
  font-weight: 600;
}

.footer-section h3 {
  font-size: 22px;
}

.footer-section h4 {
  font-size: 16px;
}

.footer-section p {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 15px;
}

.footer-section ul {
  list-style: none;
}

.footer-section ul li {
  margin-bottom: 12px;
}

.footer-section ul li a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.2s ease;
  font-size: 15px;
}

.footer-section ul li a:hover {
  color: var(--primary-color);
}

.social-links {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.social-link {
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.2s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 15px;
}

.social-link:hover {
  color: var(--primary-color);
}

.footer-bottom {
  border-top: 1px solid var(--border-color);
  padding-top: 24px;
  text-align: center;
}

.footer-bottom p {
  color: var(--text-secondary);
  font-size: 14px;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: fadeIn 0.6s ease-out;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--text-secondary);
}

/* Mobile responsive */
@media (max-width: 768px) {
  .nav-container {
    padding: 15px 16px;
    height: auto;
    min-height: 60px;
  }

  .nav-center {
    flex-direction: column;
    gap: 20px;
    width: 100%;
    align-items: center;
  }

  .nav-brand {
    order: -1;
    margin: 0;
    padding-bottom: 15px;
    border-bottom: 1px solid var(--border-color, #e3ebf0);
    width: 100%;
    justify-content: center;
  }

  .brand-logo {
    width: 60px;
    height: 60px;
  }

  /* Группируем основные навигационные ссылки */
  .nav-center>.nav-link:nth-of-type(1),
  .nav-center>.nav-link:nth-of-type(2) {
    order: 1;
  }

  /* Группируем административные элементы */
  .nav-center>.nav-link:nth-of-type(3) {
    order: 2;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid var(--border-color, #e3ebf0);
  }

  .nav-center>div:last-child {
    order: 3;
    margin-top: 5px;
  }

  .nav-link {
    width: 100%;
    max-width: 300px;
    justify-content: center;
    padding: 15px 20px;
    margin: 0 auto;
    background: var(--card-bg, #fbfcfd);
    border: 2px solid var(--border-color, #e3ebf0);
    color: var(--text-color, #3c4858);
  }

  .nav-text {
    display: block;
    font-size: 16px;
    font-weight: 600;
  }

  .nav-icon {
    font-size: 24px;
  }
}

/* Extra small mobile screens */
@media (max-width: 480px) {
  .nav-container {
    padding: 10px 12px;
  }

  .nav-center {
    gap: 15px;
  }

  .nav-link {
    padding: 12px 16px;
    font-size: 14px;
  }

  .nav-text {
    font-size: 14px;
  }

  .nav-icon {
    font-size: 20px;
  }

  .brand-logo {
    width: 50px;
    height: 50px;
  }
}

.nav-icon {
  font-size: 18px;
}

.brand-text {
  font-size: 18px;
}

.footer-container {
  padding: 30px 16px 16px;
}

.footer-content {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 24px;
  margin-bottom: 24px;
}

.main-content {
  min-height: calc(100vh - 120px);
}


/* Footer responsive */
@media (max-width: 480px) {
  .footer-content {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

/* Large screen responsive */
@media (min-width: 1201px) {
  .main-content {
    max-width: 1400px;
    padding: 0 40px;
  }

  .nav-container {
    max-width: 1400px;
    padding: 0 40px;
  }

  .footer-container {
    max-width: 1400px;
    padding: 48px 40px 24px;
  }
}

/* Extra large screen responsive */
@media (min-width: 1601px) {
  .main-content {
    max-width: 1600px;
    padding: 0 60px;
  }

  .nav-container {
    max-width: 1600px;
    padding: 0 60px;
  }

  .footer-container {
    max-width: 1600px;
    padding: 60px 60px 30px;
  }
}

/* Stream layout with ads */
.stream-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 20px -20px;
  position: relative;
  max-width: 1600px;
  /* Добавили максимальную ширину контейнера */
  margin: 20px auto;
  padding: 0 20px;
}

/* Side ads */
.side-ad {
  width: 160px;
  min-height: 600px;
  position: sticky;
  top: 20px;
}

.side-ad img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  transition: transform 0.2s ease;
}

.side-ad a:hover img {
  transform: scale(1.02);
}

/* Player section with banner */
.player-section {
  flex: 1;
  max-width: 1280px;
  /* Увеличили с 1000px до 1280px */
  position: relative;
  margin: 0 auto;
}

/* Player banner */
.player-banner {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 20;
  display: block;
  transition: opacity 0.3s ease;
}

.player-banner[data-position="top"] {
  top: 0;
}

.player-banner[data-position="bottom"] {
  bottom: 0;
}

.player-banner img {
  max-width: 100%;
  height: auto;
  border-radius: 8px 8px 0 0;
}

.player-banner:hover {
  opacity: 0.95;
}

.ad-label {
  position: absolute;
  right: 8px;
  top: 8px;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 0.75rem;
  opacity: 0.8;
}

/* Responsive layout */
@media (max-width: 1400px) {
  .side-ad {
    width: 120px;
  }
}

@media (max-width: 1200px) {
  .stream-container {
    margin: 20px 0;
  }

  .side-ad {
    display: none;
  }

  .player-section {
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .player-banner img {
    height: 60px;
    width: auto;
  }
}

.player {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.player-frame {
  background: #000;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  padding-top: 56.25%;
}

.player-frame video {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  border: 0;
  object-fit: contain;
}

/* Player overlay and controls */
.player-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, 0.4) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  z-index: 10;
}

/* Fullscreen button specific styles */
.btn-fullscreen {
  font-size: 20px !important;
  padding: 8px 10px !important;
  background: rgba(0, 0, 0, 0.7) !important;
}

.player:hover .player-overlay {
  opacity: 1;
}

.top-controls {
  display: flex;
  justify-content: flex-end;
}

.center-controls {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottom-controls {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/* Progress bar */
.progress-bar {
  width: 100%;
  height: 4px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  cursor: pointer;
  position: relative;
  transition: height 0.2s ease;
}

.player:hover .progress-bar {
  height: 6px;
}

.progress-buffer {
  position: absolute;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 2px;
}

.progress-current {
  position: absolute;
  height: 100%;
  background: var(--accent);
  border-radius: 2px;
}

/* Buttons and badges */
.big-play-btn {
  font-size: 48px;
  background: rgba(0, 0, 0, 0.6);
  border: none;
  color: white;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease;
}

.big-play-btn:hover {
  transform: scale(1.1);
}

.ad-badge {
  background: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.9rem;
}

/* Time display */
.time-display {
  color: white;
  font-size: 0.9rem;
  margin-left: 12px;
}

/* Fullscreen styles */
.player:fullscreen {
  width: 100vw;
  height: 100vh;
}

.player:fullscreen .player-frame {
  padding-top: 0;
  height: 100vh;
}

.player:fullscreen video {
  object-fit: contain;
}

/* Fullscreen fallbacks */
.player:-webkit-full-screen {
  width: 100vw !important;
  height: 100vh !important;
}

.player:-ms-fullscreen {
  width: 100vw !important;
  height: 100vh !important;
}

.player:fullscreen .player-overlay,
.player:-webkit-full-screen .player-overlay,
.player:-ms-fullscreen .player-overlay {
  padding: 40px;
}

/* Skip ad button */
.skip-ad-btn {
  background: rgba(0, 0, 0, 0.8);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  margin-left: 12px;
  transition: background-color 0.2s ease;
}

.skip-ad-btn:hover {
  background: var(--accent);
}

.player-controls {
  font-size: 0.9rem;
  color: var(--muted);
}

/* Custom player styles */
.player-frame--custom {
  padding-top: 56.25%;
  max-height: 720px;
  /* Добавили максимальную высоту */
  margin: 0 auto;
}

.player-frame--custom .ad-wrap {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
}

.player-frame--custom img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.ad-overlay {
  position: absolute;
  right: 12px;
  bottom: 12px;
  background: rgba(0, 0, 0, 0.45);
  color: #fff;
  padding: 6px 10px;
  border-radius: 8px;
  font-size: 0.95rem;
}

.player-controls-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-top: 8px;
}

.controls-left {
  display: flex;
  gap: 8px;
  align-items: center;
}

.player .btn {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  color: white;
  font-size: 16px;
  min-width: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.player .btn:hover {
  background: var(--accent);
  border-color: var(--accent);
  transform: scale(1.05);
}

.player .btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.vol input[type='range'] {
  width: 120px;
}

/* Responsive tweaks */
@media (max-width: 720px) {
  .player-frame--custom {
    padding-top: 56.25%;
  }

  .vol input[type='range'] {
    width: 80px;
  }

  .topnav {
    padding: 10px;
  }

  .content {
    padding: 12px;
  }
}

@media (max-width: 420px) {
  .player-frame--custom {
    padding-top: 66%;
  }

  .player-controls-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
  }
}

.table-wrap {
  overflow: auto;
}

.schedule-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 12px;
}

.schedule-table th,
.schedule-table td {
  text-align: left;
  padding: 12px 8px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.04);
}

.schedule-table thead th {
  color: var(--muted);
  font-size: 0.85rem;
}

.schedule-table tbody tr:hover {
  background: linear-gradient(90deg, rgba(11, 94, 215, 0.03), transparent);
}

.admin-form {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin-top: 12px;
}

.admin-form input {
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid rgba(3, 12, 30, 0.06);
  min-width: 220px;
}

.btn-primary {
  background: var(--accent);
  color: white;
  border: none;
  padding: 10px 14px;
  border-radius: 8px;
  cursor: pointer;
}

.btn-danger {
  background: var(--danger);
  color: white;
  border: none;
  padding: 6px 10px;
  border-radius: 8px;
  cursor: pointer;
}

.admin-list ul {
  list-style: none;
  margin: 12px 0 0;
  padding: 0;
  display: grid;
  gap: 8px;
}

.admin-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-radius: 8px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.01), transparent);
}

.muted {
  color: var(--muted);
}

.site-footer {
  text-align: center;
  padding: 20px;
  color: var(--muted);
}

/* Theme toggle */
.theme-toggle {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-left: auto;
}

.switch {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 26px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(3, 12, 30, 0.06);
  border-radius: 26px;
}

.slider:before {
  content: '';
  position: absolute;
  height: 20px;
  width: 20px;
  left: 3px;
  top: 3px;
  background: white;
  border-radius: 50%;
  transition: transform .2s ease;
}

.switch input:checked+.slider {
  background: var(--accent);
}

.switch input:checked+.slider:before {
  transform: translateX(20px);
}

.theme-label {
  font-size: 0.85rem;
  color: var(--muted);
}

/* Player tabs styles */
.player-tabs {
  width: 100%;
}

.tabs-header {
  display: flex;
  gap: 2px;
  margin-bottom: 1px;
  background: rgba(0, 0, 0, 0.05);
  padding: 4px;
  border-radius: 8px 8px 0 0;
}

.tab-btn {
  padding: 10px 20px;
  border: none;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  border-radius: 6px;
  font-size: 0.95rem;
  color: var(--text);
  transition: all 0.2s ease;
}

.tab-btn:hover {
  background: rgba(255, 255, 255, 0.8);
}

.tab-btn.active {
  background: var(--accent);
  color: white;
}

.tabs-content {
  background: var(--card);
  border-radius: 0 0 8px 8px;
  overflow: hidden;
}

/* Stream selector styles */
.tabs-toolbar {
  border-bottom: 1px solid rgba(3, 12, 30, 0.04);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.02), transparent);
}

.mode-btn {
  border: 1px solid rgba(3, 12, 30, 0.06);
  background: transparent;
  padding: 6px 8px;
  margin-right: 6px;
  border-radius: 6px;
  cursor: pointer;
  color: var(--muted);
}

.mode-btn.active {
  background: var(--accent);
  color: white;
  border-color: var(--accent);
}

.stream-buttons {
  display: inline-flex;
  gap: 8px;
}

.stream-item {
  border: 1px solid rgba(3, 12, 30, 0.06);
  background: white;
  padding: 8px 12px;
  border-radius: 8px;
  cursor: pointer;
  color: var(--text);
}

.stream-item.active {
  background: linear-gradient(90deg, var(--accent), #1e7be6);
  color: white;
  border-color: transparent;
  box-shadow: 0 6px 18px rgba(11, 94, 215, 0.12);
}

.stream-select {
  padding: 8px 10px;
  border-radius: 8px;
  border: 1px solid rgba(3, 12, 30, 0.06);
  background: white;
}

.player-card {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.02), transparent);
  padding: 12px;
  border-radius: 8px;
}

.player-card-header .muted {
  color: var(--muted);
}

/* Minimal, wider player — hide noisy elements and let player breathe */
.side-ad,
.player-banner,
.ad-label,
.ad-overlay,
.skip-ad-btn {
  display: none !important;
}

.content {
  max-width: 1400px;
}

.player-card {
  max-width: 1200px;
  margin: 0 auto;
  padding: 18px;
  box-shadow: 0 10px 30px rgba(3, 12, 30, 0.08);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.03), transparent);
}

.player-frame,
.player-frame--custom {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding-top: 56.25%;
  /* 16:9 */
}

.player-frame video {
  object-fit: cover;
}

@media (max-width: 1200px) {
  .content {
    max-width: 100%;
    padding: 12px;
  }

  .player-card {
    padding: 12px;
    box-shadow: 0 6px 18px rgba(3, 12, 30, 0.06);
  }
}

/* Advertisement Banners */
.page-with-ads {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: 32px;
  max-width: 1600px;
  margin: 0 auto;
  padding: 32px 24px;
  min-height: calc(100vh - 136px);
}

/* планшеты — убираем боковые колонки */
@media (max-width: 1200px) {
  .page-with-ads {
    flex-direction: column;
    align-items: center;
  }
}

/* телефоны — вообще блочная верстка */
@media (max-width: 768px) {
  .page-with-ads {
    display: block;
    padding: 16px;
  }
}

.left-ads,
.right-ads {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 16px 0;
  width: 200px;
  flex-shrink: 0;
}

.main-content-ads {
  padding: 0 16px;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  flex: 1;
}

.ad-banner {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
}

.ad-banner:hover {
  box-shadow: var(--shadow-hover);
  transform: translateY(-2px);
}

.ad-banner.vertical {
  width: 200px;
  height: 600px;
}

.ad-banner.square {
  width: 200px;
  height: 200px;
}

.ad-banner.horizontal {
  width: 100%;
  height: 100px;
  margin: 24px 0;
}

.ad-content {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.ad-gif {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: var(--border-radius);
}

.ad-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--primary-color-light), var(--hover-bg));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  font-size: 13px;
  text-align: center;
  padding: 10px;
}

.ad-label {
  position: absolute;
  top: 4px;
  right: 4px;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 2px;
}

/* Mobile responsive */
@media (max-width: 1200px) {
  .page-with-ads {
    grid-template-columns: 1fr;
    padding: 24px 16px;
    gap: 24px;
  }

  .left-ads,
  .right-ads {
    display: none;
  }

  .main-content-ads {
    padding: 0;
  }
}

@media (max-width: 768px) {
  .ad-banner.horizontal {
    height: 80px;
    margin: 16px 0;
  }
}